.task-details-container {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    background-color: #000000a3;
    bottom: 0;
    color: #172b4d;
    font-size: 14px;
    font-weight: 400;
    left: 0;
    line-height: 20px;
    overflow-y: auto;
    padding: 50px 0px 50px;
    position: fixed;
    right: 0;
    top: 0;
    z-index: 35;

    & .details-side-bar .box {
        transform: rotateZ(269deg) scaleX(0.6);
    }
}

.task-details-modal {
    border-radius: 2px;
    background-color: #f5f5f5;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    max-width: 48rem;
    min-height: 600px;
    position: relative;
    z-index: 40;

    .close-modal {
        font-size: 1.4rem;
        align-items: center;
        background-color: transparent;
        border: none;
        border-radius: 50%;
        color: #42526e;
        display: flex;
        height: 40px;
        justify-content: center;
        margin: 4px;
        padding: 4px;
        position: absolute;
        right: 0;
        top: 0;
        width: 40px;
        z-index: 2;

        &:hover {
            background-color: #00000014;
        }
    }

    .task-details-content {
        width: 100%;
        display: flex;
        justify-content: space-around;
        flex-direction: column;

        .task-icon {
            font-size: 1.5em;
        }

        .task-details-title {
            display: flex;
            align-items: center;
            gap: 10px;
            margin-inline-start: 4px;
            margin-top: 15px;
            margin-bottom: 50px;
        }

        .task-details-main {
            flex: 1;
            padding-right: 10px;


        }



        .details-side-bar {

            .additives-modal,
            .actions-modal,
            .join-section {
                display: grid;
                grid-template-columns: 1fr 1fr;
                column-gap: 10px;

                h3 {
                    grid-column: 1/3;
                }
            }
        }

        @media (min-width:650px) {

            flex-direction: row;

            .details-side-bar {

                .additives-modal,
                .actions-modal,
                .join-section {
                    display: block;

                }
            }
        }







    }
}